<template>
	<div>


		

			<!--영정사진crop pop-->
			<div class="photocrop active">
				<div class="photocrop_pop_inner">
					<h1>사진편집하기</h1>
					<p>원하는 부분이 나올 수 있도록 캔버스를 이동하신 후 <br>등록을 클릭해 주세요.</p>
				<input type="checkbox" :checked="flagNewDisplay === 0" @click="flagNewDisplay = 0"><span class="chkStyle">구형</span>
				<input type="checkbox"  :checked="flagNewDisplay === 1"  @click="flagNewDisplay = 1">신형
					<div class="cropspace">

						<cropper v-if="flagNewDisplay === 1"
							class="cropper"
							:src="img"
							:stencil-props="{
								aspectRatio: 11/14
							}"
							@change="change"
						/>
						<cropper v-if="flagNewDisplay === 0"
							class="cropper"
							:src="img"
							:stencil-props="{
								aspectRatio: 3/4
							}"
							@change="change"
						/>
					</div>
				
						
				</div>
				<button class="btn_photocrop" @click="regist">등록</button>
				<button class="btn_service_colse" @click="close">닫기</button>
			</div>
	</div>
	
</template>

<script>
import { Cropper } from 'vue-advanced-cropper'

export default {
	components: {
		Cropper,
	},
	props:['files','methods','methodsClose'],
	
    data(){
        return{
			flagNewDisplay:0,
			img: null,

			canvas:null,
			returnFiles:[]
		}
	},
	mounted(){
		
			this.flagNewDisplay = parseInt(this.$store.getters.getObituaryDisplayType)
			 this.img = URL.createObjectURL(this.files[0]);
	},
	methods: {
		change({ coordinates, canvas }) {
			console.log(coordinates, canvas)
			this.canvas = canvas
		},
		regist(){
			
			this.canvas.toDataURL();
			const context = this
			this.canvas.toBlob(blob => {
				
				context.returnFiles = []
				console.log(blob)
				context.returnFiles.push(new File([blob], context.files[0].name, { type: "image/jpeg", lastModified: Date.now() }))
				context.methods(context.returnFiles,this.flagNewDisplay)
				this.$store.commit('OBITUARY_DISPLAY_TYPE',this.flagNewDisplay) 
			});;

		},
		close(){
			
			this.methodsClose()
		}
	},
}
</script>

<style scoped>
.cropper {
	height: 600px;
	background: #DDD;
}
</style>