_v("제 4 조 (약관 외 준칙)"),_c('br'),_vm._v(" 이 약관에 명시되지 않은 사항에 대해서는 정보통신망 이용촉진 및 정보보호 등에 관한 법률, 약관의 규제에 관한 법률, 전기통신사업법 등 관계법령 및 회사가 정한 서비스의 세부이용지침 등의 규정에 따릅니다. "),_c('br'),_c('br'),_vm._v("제 5 조 (이용계약의 성립)"),_c('br'),_c('br'),_vm._v("1)서비스 이용계약은 이용고객이 이 약관 및 “개인정보의 수집 및 이용\"에 “동의합니다\"를 선택하고, 회사가 정한 신청양식을 작성하여 서비스의 이용을 신청한 후, 회사가 이를 승낙함으로써 성립합니다. "),_c('br'),_vm._v("2)이용고객이 전항의 \"동의합니다\"를 선택하고 신청양식을 작성하는 것은 이 약관 및 “개인정보의 수집 및 이용에 대한 내용을 숙지하고, 회사가 서비스 이용을 위해 운영하는 각종 정책(예 : 개인정보처리방침, 저작권 정책, 스팸정책 등)과 수시로 공지하는 사항을 준수하는 것에 대해 동의하는 것으로 봅니다. "),_c('br'),_c('br'),_vm._v("제 6 조 (이용신청)"),_c('br'),_c('br'),_vm._v("1)회원이 신청양식에 기재하는 회원정보는 이용고객의 실제정보인 것으로 간주되고, 실제정보를 입력하지 않은 회원은 법적인 보호를 받을 수 없으며 서비스 이용에 제한을 받을 수 있습니다. "),_c('br'),_vm._v("2)만14세 미만의 아동이 서비스를 이용하기 위해서는 회사가 요구하는 소정의 법정대리인의 동의절차를 거쳐야 합니다. "),_c('br'),_vm._v("3)타인의 개인정보를 도용하는 등 부정한 목적과 방법으로 이용신청을 한 회원의 ID는 사전 통지없이 삭제될 수 있으며, 당해 회원은 관계법령에 따라 처벌을 받을 수 있습니다. "),_c('br'),_c('br'),_vm._v("제 7 조 (이용신청에 대한 승낙 및 제한)"),_c('br'),_c('br'),_vm._v("1)회사는 서비스의 이용을 신청한 이용고객에 대하여 업무상ㆍ기술상 지장이 없는 한 접수순서에 따라 서비스의 이용을 승낙합니다. "),_c('br'),_vm._v("2)회사는 다음 각 호에 해당하는 이용신청에 대하여는 승낙을 하지 않을 수 있습니다. "),_c('br'),_vm._v("①기술상 서비스 제공이 불가능한 경우 "),_c('br'),_vm._v("②신청양식을 허위로 기재한 경우 "),_c('br'),_vm._v("③신청양식의 기재사항을 누락하거나 오기하여 신청하는 경우 "),_c('br'),_vm._v("④사회의 안녕질서 또는 미풍양속을 저해하거나 저해할 목적으로 신청한 경우 "),_c('br'),_vm._v("⑤회원의 귀책사유에 의하여 회원자격을 상실한 적이 있는 경우. 다만, 자격상실 후 6개월 이상 경과한 자로 회사의 회원 재가입 승낙을 받은 경우는 예외로 합니다. "),_c('br'),_vm._v("⑥기타 회사가 정한 이용요건에 충족되지 않았을 경우 "),_c('br'),_vm._v("회원은 서비스를 이용하면서 신청양식에 기재한 회원정보가 변경되었을 경우 지체없이 회원정보를 수정하여야 합니다. 회원정보를 수정하지 않음으로 인하여 발생하는 모든 책임은 회원에게 있습니다. "),_c('br'),_c('br'),_vm._v("제 9 조 (서비스의 이용)"),_c('br'),_c('br'),_vm._v("1)회사는 회원의 이용신청을 승낙한 때부터 서비스를 개시합니다. 다만, 일부 서비스의 경우에는 지정된 일자부터 서비스를 개시합니다. "),_c('br'),_vm._v("2)회사의 업무상ㆍ기술상의 장애로 인하여 서비스를 개시하지 못하는 경우에는 서비스에 공지하거나 회원에게 이를 통지합니다. "),_c('br'),_vm._v("3)서비스의 이용은 연중무휴 1일 24시간을 원칙으로 합니다. 다만, 회사의 업무상ㆍ기술상 또는 서비스 운영정책상 서비스가 일시 중지될 수 있습니다. 이러한 경우 회사는 사전 또는 사후에 이를 공지합니다. "),_c('br'),_vm._v("4)회사는 서비스를 일정 범위로 분할하여 각 범위 별로 이용 가능한 시간을 별도로 정할 수 있으며 이 경우 그 내용을 공지합니다. "),_c('br'),_vm._v("5)회사는 서비스 내의 개별 서비스에 대한 별도의 약관을 둘 수 있으며, 개별 서비스에서 별도로 적용되는 약관에 대한 동의는 회원이 개별 서비스를 최초로 이용할 경우 별도의 동의절차를 거치게 됩니다. "),_c('br'),_c('br'),_vm._v("제 10 조 (서비스의 변경 및 중지)"),_c('br'),_c('br'),_vm._v("1)회사는 서비스(개별 서비스 포함)를 변경하여야 하는 상당한 이유가 있는 경우 변경될 서비스의 내용 및 제공일자를 제25조에서 정한 방법으로 회원에게 통지하고 서비스를 변경하여 제공할 수 있습니다. "),_c('br'),_vm._v("2)회사는 회원의 ID가 다음 각 호에 해당되는 경우 회원과 협의하여 회원의 ID를 변경할 수 있습니다. "),_c('br'),_vm._v("①ID가 회원의 주민등록번호 등으로 등록되어 사생활 침해가 우려되는 경우 "),_c('br'),_vm._v("②타인에게 혐오감을 주거나 미풍양속에 어긋나는 경우 "),_c('br'),_vm._v("③ID가 타회원과 중복되어 서비스 제공이 곤란한 경우 "),_c('br'),_vm._v("④기타 합리적인 사유가 있는 경우 "),_c('br'),_vm._v("3)회사는 다음 각호에 해당하는 경우 서비스의 전부 또는 일부를 제한하거나 중지할 수 있습니다. 이 경우 회사는 제한 또는 중단 사유를 사전에 공지합니다 "),_c('br'),_vm._v("①서비스용 설비의 보수 등 공사로 인한 부득이한 경우 "),_c('br'),_vm._v("②회사가 통제하기 곤란한 사정으로 불가피하게 서비스 제한 또는 중단이 필요한 경우 "),_c('br'),_vm._v("③회원이 회사의 영업활동을 방해하는 경우 "),_c('br'),_vm._v("④정전, 제반 설비의 장애 또는 서비스 이용량의 폭주 등으로 정상적인 서비스 이용에 지장이 있는 경우 "),_c('br'),_vm._v("⑤새로운 서비스로의 교체, 개별 서비스를 회사가 운영하는 다른 사이트로 이전하는 등 회사의 서비스 운영정책상 서비스의 제한 또는 중단이 필요하다고 판단하는 경우 "),_c('br'),_vm._v("⑥제휴업체(BP)와의 계약종료 등과 같은 회사의 제반 사정으로 서비스를 유지할 수 없는 경우 "),_c('br'),_vm._v("⑦기타 천재지변, 국가비상사태 등 불가항력적 사유가 있는 경우 "),_c('br'),_vm._v("4)회사는 비실명인 회원, 만14세 미만 회원, 청소년보호법상의 보호대상인 회원, 만 19세 미만의 회원, 외국인 회원, 법인 및 단체 회원, 폰번호 회원 등 회원별로 서비스의 이용을 제한할 수 있으며, 이러한 서비스 제한에 대한 사항은 개별 서비스에서 별도로 안내 또는 공지합니다. "),_c('br'),_c('br'),_vm._v("제11조 (컨텐츠 서비스)"),_c('br'),_c('br'),_vm._v("1)회사는 유료 컨텐츠 서비스를 제공함에 있어 회사의 정책에 따라 회원에게 유료 컨텐츠 서비스의 이용기한을 정할 수 있습니다. 유료 컨텐츠 서비스의 이용기한은 각 유료 컨텐츠 서비스 세부이용지침에서 정하거나 각 유료 컨텐츠 서비스 결제화면 등에 게시하여 회원에게 공지합니다. "),_c('br'),_vm._v("2)회원은 유료 컨텐츠 서비스를 이용하기 전에 각 유료컨텐츠 서비스의 세부이용지침에서 공지하는 컨텐츠 서비스 이용 단말기의 사양, 유ㆍ무선 통신망의 품질 등이 해당 컨텐츠 서비스의 이용에 적합한지 여부를 사전에 확인하여야 합니다. "),_c('br'),_vm._v("3)회사의 귀책사유로 인하여 회원이 유료 컨텐츠 서비스 이용기간 동안 컨텐츠 서비스를 제공받을 수 없게 되는 경우, 회사는 유료 컨텐츠서비스 이용잔여기간 동안 이용할 수 있는 동일ㆍ유사한 컨텐츠를 다시 제공하거나 제11조 제4항의 산식에 따라 보상합니다. "),_c('br'),_c('br'),_vm._v("제12조 (정보 제공 및 광고 게재)"),_c('br'),_c('br'),_vm._v("1)회사는 서비스를 운영함에 있어 각종 정보를 서비스 화면에 게재하거나 메일, 쪽지, SMS등의 방법으로 회원에게 제공할 수 있습니다. "),_c('br'),_vm._v("2)회사는 서비스의 운영과 관련하여 광고 등을 서비스 화면에 게재하거나, 메일, 쪽지, SMS등을 통하여 발송할 수 있습니다. 다만, 회사는 회원에게 메일, 쪽지, SMS 등을 발송하는 경우 수신 동의 여부를 확인하여, 수신 동의한 회원에 한하여 이를 발송합니다. "),_c('br'),_vm._v("3)회원이 서비스상에 게재되어 있는 광고를 이용하거나 서비스를 통한 광고주의 판촉활동에 참여하는 등의 방법으로 교신 또는 거래를 하는 것은 전적으로 회원과 광고주간의 문제입니다. 만약, 회원과 광고주간에 문제가 발생할 경우에도 회원과 광고주가 직접 해결하여야 하며, 이와 관련하여 회사는 어떠한 책임도 지지 않습니다. "),_c('br'),_c('br'),_vm._v("제18조 (회원의 게시물에 대한 책임)"),_c('br'),_c('br'),_vm._v("1)회사는 회원이 서비스에 게시하거나 전달하는 서비스내의 게시물이 다음 각 호의 경우에 해당한다고 판단되는 경우 사전 통지 없이 삭제할 수 있으며, 이에 대해 회사는 어떠한 책임도 지지 않습니다. "),_c('br'),_vm._v("①회사, 다른 회원 또는 제3자를 비방하거나 중상모략으로 명예를 손상시키는 내용인 경우 "),_c('br'),_vm._v("②공공질서 및 미풍양속에 위반되는 내용의 게시물에 해당하는 경우 "),_c('br'),_vm._v("③게시물의 내용이 범죄적 행위에 결부된다고 인정되는 내용인 경우 "),_c('br'),_vm._v("④회사의 저작권, 제3자의 저작권 등 기타 타인의 권리를 침해하는 내용인 경우 "),_c('br'),_vm._v("⑤회사가 정한 개별 서비스별 세부이용지침에 반하는 내용인 경우 "),_c('br'),_vm._v("⑥회사에서 제공하는 서비스와 관련 없는 내용인 경우 "),_c('br'),_vm._v("⑦불필요하거나 승인되지 않은 광고, 판촉물을 게재하는 경우 "),_c('br'),_vm._v("⑧타인의 ID, 명의 등을 무단으로 도용하여 작성한 내용이거나, 타인이 입력한 정보를 무단으로 위•변조한 내용인 경우 "),_c('br'),_vm._v("⑨동일한 내용을 중복하여 다수 게시하는 등 게시의 목적에 어긋나는 경우 "),_c('br'),_vm._v("⑩기타 관계 법령 및 회사의 개별 서비스별 세부이용지침 등에 위반된다고 판단되는 경우 "),_c('br'),_vm._v("2)회사는 개별 서비스별로 게시물과 관련된 세부이용지침을 별도로 정하여 시행할 수 있으며, 회원은 그 지침에 따라 게시물(회원간 전달 포함)을 게재하여야 합니다. "),_c('br'),_c('br'),_vm._v("제12조 (휴면계정 관리)"),_c('br'),_c('br'),_vm._v("1)회사는 장기 비로그인 회원에게 배달되는 불법스팸메일 또는 회원의 메일함이 방치됨으로써 발생하는 불필요한 저장공간의 낭비를 줄이고 서비스 이용을 원활히 하기 위해서 휴면계정 관리정책을 실시합니다. "),_c('br'),_vm._v("2)회사는 회원이 3개월 동안 서비스에 로그인하지 않는 경우 메일 수신 정지 조치를 취할 수 있으며, 이후 9개월 동안(연속하여 12개월 동안) 서비스에 로그인하지 않는 경우 서비스 이용계약을 해지하거나, 회원의 메일함을 초기화할 수 있습니다. "),_c('br'),_vm._v("3)제2항에 따른 서비스 이용 계약의 해지 또는 메일함 초기화로 인한 데이터(예: 주소록, 메일 등)의 삭제에 대한 책임은 회원이 부담하며, 회사는 고의 또는 과실이 없는 한데이터 삭제에 따른 책임을 부담하지 않습니다. "),_c('br'),_c('br'),_vm._v("제23조 (회사의 의무)"),_c('br'),_c('br'),_vm._v("1)회사는 서비스 제공과 관련하여 알고 있는 회원의 회원정보를 본인의 동의없이 제3자에게 제공하지 않습니다. "),_c('br'),_vm._v("2)회사는 회원의 회원정보를 보호하기 위해 보안시스템을 구축 운영하며, \"개인정보처리방침\"을 공지하고 준수합니다. 또한, 회사는 \"개인정보처리방침\" 따라 회원정보를 처리함에 있어 안정성 확보에 필요한 기술적 및 관리적 대책을 수립ㆍ운영합니다. "),_c('br'),_vm._v("3)회사는 서비스와 관련한 회원의 불만사항이 접수되는 경우 이를 신속하게 처리하여야 하며, 신속한 처리가 곤란한 경우 그 사유와 처리 일정을 서비스 화면에 게재하거나 메일 등을 통하여 회원에게 통지합니다. "),_c('br'),_vm._v("4)회사가 제공하는 서비스로 인하여 회원에게 손해가 발생한 경우 그러한 손해가 회사의 고의나 과실에 기해 발생한 경우에 한하여 회사에서 책임을 부담하며, 그 책임의 범위는 통상손해에 한합니다. "),_c('br'),_c('br'),_vm._v("제13조 (회원의 의무)"),_c('br'),_c('br'),_vm._v("1)회원은 관계법령, 약관, 서비스 이용안내 및 서비스상에 공지한 주의사항, 회사가 서비스 이용과 관련하여 회원에게 통지하는 사항 등을 준수하여야 하며, 기타 회사의 업무에 방해되는 행위를 하여서는 아니 됩니다. "),_c('br'),_vm._v("2)회원은 회사에서 공식적으로 인정한 경우를 제외하고는 서비스를 이용하여 상품을 판매하는 영업 활동을 할 수 없으며, 특히 해킹, 광고를 통한 수익, 음란사이트를 통한상업행위, 상용소프트웨어 불법배포 등을 할 수 없습니다. 이를 위반하여 발생한 영업 활동의 결과 및 손실, 관계기관에 의한 구속 등 법적 조치 등에 관해서는 회사가책임을 지지 않으며, 회원은 이와 같은 행위와 관련하여 회사에 대하여 손해배상 의무를 집니다. "),_c('br'),_vm._v("3)회원은 신청양식 기재 시 또는 회원정보 변경 시 모든 사항을 사실에 근거하여 작성하여야 하며, 허위 또는 타인의 개인정보를 이용한 사실이 발견된 경우 회원은 서비스 이용과 관련한 일체의 권리를 주장할 수 없습니다. "),_c('br'),_vm._v("4)회원은 서비스의 이용권한, 기타 서비스 이용계약상의 지위를 타인에게 양도, 증여할 수 없으며 이를 담보로 제공할 수 없습니다. "),_c('br'),_vm._v("5)회원은 회원 ID 및 비밀번호를 철저히 관리하여야 하며, 관리소홀, 부정사용 등에 의하여 발생하는 모든 결과에 대한 책임은 회원 본인이 부담하며, 회사는 고의 또는 과실이 없는 한 이에 대한 어떠한 책임도 부담하지 않습니다. "),_c('br'),_vm._v("6)회원은 본인의 ID 및 비밀번호를 제3자에게 이용하게 하여서는 아니되며, 회원 본인의 ID 및 비밀번호를 도난당하거나 제3자가 사용하고 있음을 인지하는 경우에는 즉시 비밀 번호를 변경하여야 하며, 해당 사실을 회사에 통지하고 회사가 안내하는 바에 따라야 합니다. "),_c('br'),_vm._v("7)회원은 이용계약을 체결하면서 부여받은 ID를 변경할 수 없으며, 부득이한 사유로 인하여 변경하고자 하는 경우에는 이용계약을 해지하고 재가입하여야 합니다. "),_c('br'),_c('br'),_vm._v("제14조 (회원정보의 보호 및 이용)"),_c('br'),_c('br'),_vm._v("1)회사는 적법하고 공정한 수단에 의하여 이용계약의 성립 및 이행에 필요한 최소한의 범위 내에서 \"개인정보처리방침\"에 따라 이용고객의 회원정보를 수집합니다. "),_c('br'),_vm._v("2)회사의 \"개인정보처리방침\"은 회사에 링크된 다른 웹사이트(회사가 운영하지 않는 웹사이트를 말합니다. 이하 같습니다)에서는 적용되지 않습니다. "),_c('br'),_vm._v("3)회사는 전기통신사업법, 통신비밀보호법, 정보통신망 이용촉진 및 정보보호 등에 관한 법률, 개인정보보호법 등 관계법령에 따라 행정기관이나 수사기관 등에서 회원의 회원정보의 열람이나 제출을 요청하는 경우에는 이를 제공할 수 있습니다. "),_c('br'),_vm._v("4)회사는 회