<template>
    <div class="music_layout active"><!-- 장례식장 사이드메뉴-->
		<div class="address_search_inner">
			<h1>음악추가</h1>
			<table>
			<colgroup>
				<col width="85%">
				<col width="15%">
			</colgroup>
			<tbody>
			<tr style="border-bottom:1px solid #cccccc;">
				<td><button class="btn_up" ref="fileuploadbtn" @click="fileUpload"><div class="btn_up_txt">{{files.length===0?'음악 업로드':files[0].name}}</div></button>
					<input type="file" ref="fileupload" @change="changeFiles" multiple style="display:none;" accept="audio/*"/></td>
				<td style="text-align:right;"><button class="address_register" @click="addMusic"></button></td>
			</tr>
			</tbody>
			</table> 
			<div class="address" style="margin-top:10px;">
			<table cellspacing="0" cellpadding="0" border="0">	
			<tr v-for="(music,index) in getMusicList" :key="index" style="color:#999; border-bottom:1px solid #ccc;"  @click="registAddress(address)">
				<template v-if="music.musicFile.length>0">
				<td width="90%" style="padding:8px 0 8px;">{{musicName(music.musicFile)}}</td>
				<td style="text-align:right;"><button class="address_register" @click="selectMusic(music)" ></button></td>
				</template>
			</tr>
			</table>  
		</div>
		</div>
		<button class="btn_address_colse" @click="close">닫기</button>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
	props:['methods'],
	computed:{
		...mapGetters(['getMusicList']),
	},
    data(){
        return{
			music:{
				id:null,
				musicMember:null
			},
			files:[]
		}
	},
	methods:{
		musicName(file){
			return file[0].fileName
		},
        changeFiles(event){
			
			if(event.target.files.length>0){
				this.files = event.target.files
			}
        },
        fileUpload(){
           this.$refs.fileupload.click();
		},
		close(){
			this.methods()
		},
		selectMusic(music){
			
			this.methods(music)
		},
		init(){

			this.music = {
				id:null,
				musicMember:null
			}
			this.files =[]
		},
		addMusic(){
			const parameter = {
				music: this.music,
				files:this.files
			}
			this.$store.dispatch("REQUEST_REGIST_MUSIC",parameter)
			this.init()
		},
		deleteMusic(id){
			const parameter = {
				id: id
			}
			this.$store.dispatch("REQUEST_DELETE_MUSIC",parameter)

		}
	}
}
</script>